@import url(https://fonts.googleapis.com/css?family=Open+Sans|Mrs+Sheppards);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: #1c2532;
  overflow-x: hidden;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

div, h1, h2, h3, h4, h5, h6 {
  box-sizing: border-box;
}

hr {
  background-image: -webkit-radial-gradient(#728041, transparent 60%);
  height: 1px;
  width: 100%;
  border: none;
  margin: 10px 0;
}

.app {
  box-sizing: border-box;
  width: 100vw;
  min-height: 100vh;

  display: -webkit-flex;

  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}


.charts-container {
  max-width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-justify-content: center;
          justify-content: center;
  margin: 0 auto;
}

.content-header {
  width: 100%;
  padding: 30px 0;
  font-family: Open Sans;
  font-size: 30px;
  color: #2a352f;
}

.content {
  margin: 0 auto;
  width: 1280px;

  max-width: 100%;
  overflow-x: scroll;

  padding: 30px 20px;
  font-family: Open Sans;
  font-size: 16px;
}


.js-plotly-plot {
  display: block;
  width: 100%;
  height: 100%;
}


@media (max-width: 500px) {
  .header,
  .content,
  .win-rate,
  .player-faction { 
    width: 100vw;
    overflow-x: hidden;
  }

  .player-faction .main-svg{
    margin-left: -58px;
  }

}
